@mixin widthHeigth($width, $height) {
  width: $width;
  height: $height;
}

@mixin resetStyleButton {
  background-color: inherit;
  border: none;
}

@mixin resetList {
  list-style: none;
  margin: 0;
  padding: 0;
}

@mixin wrapper-size-position($string) {
  width: display-size($string);
  margin: 0 auto;
}